<template>
   <group  class="jq-mt">
  <popup-picker :title="dateTile" :data="timeList" :value.sync="time" ></popup-picker>
   </group>
  <group  class="jq-mt">
    <div class="chart" v-ebar="barData" id="barDataCostADD" ></div>
  </group>
  <group  class="jq-mt">
    <div class="chart" v-ebarline="barLineData" ></div>
  </group>
</template>
<script>
  //ADD，SMD公用同一个页面
  import {Group,PopupPicker} from 'vux/src/components'
  import { getList } from '../../../untils/api'
  export default {
    components: {
       PopupPicker,
      Group
  },
  data () {
    return {
      barData:{},
      barLineData:{},
       dateTile:'日期',
       time: formatData.defaultTime,
       timeList: formatData.dateList,
      permissions:localStorage.getItem("permissions")
    }
  },
  ready(){
      document.title='成本管理';
  },
   events: {
  'eChartClick': function (data) {
      switch(data.id){
        case "barDataCostADD":
        //根据accessToken判断调用ADD还是SMD

         if(this.permissions=='202')//add
           this.$route.router.go({name: 'coststructureadd'});
         else if(this.permissions=='203'||this.permissions=='201')
           this.$route.router.go({name: 'coststructuresmd'});
        break;
        default:
        break;
      }
  }
  },
  methods: {
    async getData() {
       commit('UPDATE_LOADING', true)
       let id ='';
      if(this.permissions=='202'){
        id = "APP10204"
      }else if(this.permissions=='201'){
          id = "APP10301a";
      }else if(this.permissions=='203'){
         id = "APP10301b";
      }


     let date=formatData.dateConversion(this.time);
   let dataTit = await getList(id,{date_:date});
      if(dataTit.success){
       
        //表头：
        //表格:
        const TableList = dataTit.data;
        this.barData = {};
        this.barData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
         commit('UPDATE_LOADING', false)
      }else{
         commit('UPDATE_LOADING', false)
        console.log(dataTit.message);
      }

    },
    async getBarLineData() {
      commit('UPDATE_LOADING', true)
      let id ='';
      if(this.permissions=='202'){
        id = "APP10206c";
      }else if(this.permissions=='201'){
          id = "APP10206a";
      }else if(this.permissions=='203'){
         id = "APP10206b";
      }

   let date=formatData.dateConversion(this.time);
   let dataTit = await getList(id,{date_:date});
      if(dataTit.success){
      
        //表头：
        //表格:
        const TableList = dataTit.data;
        this.barLineData = {};
        this.barLineData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
        commit('UPDATE_LOADING', false)
      }else{
        commit('UPDATE_LOADING', false)
        console.log(dataTit.message);
      }
    }
  },
   watch: {
     time (newtime, oldtime) {
      this.getData();
      this.getBarLineData();
       formatData.setDateTime(newtime);
    }
  },
  created: function () {
    this.barData = {};
    this.barData.dataset = [];
    this.barLineData = {};
    this.barLineData.dataset = [];
    this.getData();
    this.getBarLineData();
  }
  }

</script>






